import React, { Component } from 'react';
import Account from './Account';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import PropTypes from 'prop-types';
import { LOGOUT_URL } from '../listrUrls';

import { performLogin, setUserToken, performLogout } from '../actions/userActions';



class Navbar extends Component {

  constructor(props){
    super(props);
    this.onLogout = this.onLogout.bind(this);
  }

  onLogout(){
    localStorage.removeItem('token');
    this.props.performLogout(LOGOUT_URL);
  }

  render(){

    const styles = {
      root: {
        flexGrow: 1,
      },
      grow: {
        flexGrow: 1,
      },
      menuButton: {
        marginLeft: -12,
        marginRight: 20,
      },
    };

    return(
      <div className={styles.root}>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="title" color="inherit" style={{ flex: 1 }}>
              CollabPads
            </Typography>
            <Button color="inherit" onClick={this.onLogout}>Logout</Button>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}
Navbar.proptypes = {
  performLogout: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({

});

export default connect(mapStateToProps, { performLogout })(Navbar);



