import React, { Component } from 'react';
import { performLogin, setUserToken } from './actions/userActions';
import Main from './Main';
import Login from './Login';
import { createMuiTheme, createPalette } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import { Switch, Route } from 'react-router-dom';


class App extends Component {



  constructor(props){
    super(props);
    this.checkIfLoggedIn = this.checkIfLoggedIn.bind(this);
  }

  componentWillMount(){
    this.checkIfLoggedIn();

  }

  checkIfLoggedIn(){
    var cachedToken = localStorage.getItem("token");
    if(cachedToken !== null && cachedToken !== undefined){
      this.props.setUserToken(cachedToken);
      console.log("LOGGED IN");
      return true;
    }
    return false;
  }

  render(){
    var showComponent;
    const muiTheme = createMuiTheme({
      palette: {
        primary: {
          main: '#43a047',
        },
        secondary: {
          main: '#f44336',
        },
        danger: {
          main: '#ff1744',
        },
      }
    });
    if(this.props.token){
      showComponent = <Main />
    }else{
      showComponent = <Login />
    }
    return(
      <MuiThemeProvider theme={muiTheme}>
        {showComponent}
      </MuiThemeProvider>
    );
  }

}

const mapStateToProps = state => ({
  token: state.users.token,
});

export default connect(mapStateToProps, { performLogin, setUserToken })(App);



