import React, { Component } from 'react';
import { deleteList, fetchLists } from '../actions/listActions';
import { fetchItems } from '../actions/itemActions';
import { connect } from 'react-redux';
import { LISTS_URL, ITEMS_URL } from '../listrUrls';
import PropTypes from 'prop-types';
import ListDetail from './ListDetail';
import Grid from '@material-ui/core/Grid';
import classNames from 'classnames';

class Lists extends Component {

  constructor(props){
    super(props);
    this.state = {
      currentList: {},
      modalOpen: false,
    }
    this.showItems = this.showItems.bind(this);
    this.toggleDetails = this.toggleDetails.bind(this);
  }

  showItems(e, data, listType){
    e.preventDefault();
    this.setState({currentList: data});
    this.props.fetchItems(ITEMS_URL, data.static_id, this.props.token);
    if(!this.state.modalOpen){
      this.toggleDetails();
    }
  }

  toggleDetails(){
    this.setState((prevState) => ({modalOpen: !prevState.modalOpen}));
  }

  render () {
    var hideRemove;
    var detailModal;
    var userCount;

    const styles = theme => ({
      layout: {
        width: 'auto',
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(1100 + theme.spacing.unit * 3 * 2)]: {
          width: 1100,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
      cardGrid: {
        padding: `${theme.spacing.unit * 8}px 0`,
      },
    });


    var data = this.props.data.map(list => (
      <ListDetail data={list} key={list.static_id} listType={this.props.listType}/>
    ));
    if(this.props.data.length <= 0 && this.props.listType !== "shared"){
      data = <h3>You Have No Pads Made. Create and Share One Now!</h3>
    }
    return(
      <div className={classNames(styles.layout, styles.cardGrid)}>
          {/* End hero unit */}
        <Grid container spacing={24}>
          {data}
        </Grid>
      </div>
    );
  }
}

Lists.proptypes = {
  deleteList: PropTypes.func.isRequired,
  fetchLists: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  items: state.items.items,
  itemsLoading: state.items.loading
});

export default connect(mapStateToProps, { deleteList, fetchLists, fetchItems })(Lists);



