import React, { Component } from 'react';
import { connect } from 'react-redux';
import { performLogin, setUserToken, performLogout } from '../actions/userActions';
import PropTypes from 'prop-types';
import { LOGIN_URL, LOGOUT_URL, REGISTER_URL } from '../listrUrls';
import CSRFToken from './csrftoken';
import Button from '@material-ui/core/Button';

class Account extends Component {

  constructor(props){
    super(props);
    this.onLogout = this.onLogout.bind(this);
    this.checkIfLoggedIn = this.checkIfLoggedIn.bind(this);

  }
  componentDidMount(){
    this.checkIfLoggedIn();
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value})
  }

  checkIfLoggedIn(){
    var cachedToken = localStorage.getItem("token");
    if(cachedToken !== null && cachedToken !== undefined){
      this.props.setUserToken(cachedToken);
      return true;
    }
    return false;
  }


  render(){
    return
  }

}

Account.proptypes = {
  performLogin: PropTypes.func.isRequired,
  performLogout: PropTypes.func.isRequired,
  setUserToken: PropTypes.func.isRequired
}

const mapStateToProps = state => ({
  userToken: state.users.token,
  isLoginningIn: state.users.loading
});

export default connect(mapStateToProps, { performLogin, setUserToken, performLogout })(Account);



