import { USER_LOGIN, USER_LOGIN_SUCCESS, USER_HAS_LOGIN, USER_LOGOUT_SUCCESS, USER_DATA_SUCCESS } from "../actions/types";

export default function userReducer(state = {token: null, loading: false, username: null}, action){
    switch (action.type) {
      case USER_LOGIN:
        return {
          ...state,
          loading: true
        }
      case USER_HAS_LOGIN:
        localStorage.setItem('token', action.payload);
        return {
          loading: false,
          token: action.payload
        }
      case USER_LOGIN_SUCCESS:
        if(action.token){
          localStorage.setItem('token', action.token);
          window.location.reload();
          return {
            loading: false,
            token: action.token
          }
        }else{
          alert("Something went wrong. Please try again.")
        }
      case USER_DATA_SUCCESS:
        if(action.payload.username){
          return {
            ...state,
            loading: false,
            username: action.payload.username
          }
        }else{
          alert("Something went wrong. Please try again.")
        }
      case USER_LOGOUT_SUCCESS:
        return {
          loading: false,
          token: null
        }
      default:
        return state
    }
}



