import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { performLogin } from './actions/userActions';
import classNames from 'classnames';
import { LOGIN_URL, REGISTER_URL } from './listrUrls';

import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';


const styles = theme => ({
  layout: {
    width: 'auto',
    display: 'block', // Fix IE11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    textAlign: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  form: {
    width: '100%', // Fix IE11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
});

class Login extends Component{
  constructor(props) {
    super(props);
    this.state = {listrUsername: '', listrPassword: '', listrPassword2: '', signIn: true};
    this.onLogin = this.onLogin.bind(this);
    this.onRegister = this.onRegister.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggleForm = this.toggleForm.bind(this);
  }

  onLogin(e){
    e.preventDefault();
    const userData = {
      "username": this.state.listrUsername,
      "password": this.state.listrPassword
    }

    this.props.performLogin(LOGIN_URL, userData);
  }

  toggleForm(){
    console.log(this.state.signIn);
    this.setState((prevState) => ({
      signIn: !prevState.signIn
    }));
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value})
  }

  onRegister(e){
    e.preventDefault();

    const userData = {
      "username": this.state.listrUsername,
      "password1": this.state.listrPassword,
      "password2": this.state.listrPassword2
    }

    this.props.performLogin(REGISTER_URL, userData);
  }

  render(){
    const { classes } = this.props;

    var form;
    if(!this.state.signIn){
      form = (<Paper className={classes.paper}>
        <Typography variant="headline">Sign Up</Typography>
        <form onSubmit={(e) => this.onRegister(e)}>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="listrUsername">Username</InputLabel>
            <Input id="listrUsername" name="text" autoFocus onChange={(e) => this.handleChange(e)}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input
              name="password"
              type="password"
              id="listrPassword"
              autoComplete="current-password"
              onChange={(e) => this.handleChange(e)}
            />
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password2">Confirm Password</InputLabel>
            <Input
              name="password2"
              type="password"
              id="listrPassword2"
              autoComplete="current-password"
              onChange={(e) => this.handleChange(e)}
            />
          </FormControl>
          <Button
            type="submit"
            fullWidth
            variant="raised"
            color="primary"
            >
            Create Account
          </Button>
          <Button className={classes.submit}
            color="default"
            onClick={this.toggleForm}
            >
            or Sign In
          </Button>
        </form>
      </Paper>);
    }else{
      form = (<Paper className={classes.paper}>
        <Typography variant="headline">Sign In</Typography>
        <form onSubmit={(e) => this.onLogin(e)}>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="listrUsername">Username</InputLabel>
            <Input id="listrUsername" name="text" autoFocus onChange={(e) => this.handleChange(e)}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input
              name="password"
              type="password"
              id="listrPassword"
              autoComplete="current-password"
              onChange={(e) => this.handleChange(e)}
            />
          </FormControl>
          <Button
            type="submit"
            fullWidth
            variant="raised"
            color="primary"
            >
            Sign In
          </Button>
          <Button className={classes.submit}
            color="default"
            onClick={this.toggleForm}
            >
            or Create an Account
          </Button>
        </form>
      </Paper>);
    }

    return(
      <Fragment>
        <CssBaseline />
        <main className={classes.layout}>
          {form}
        </main>
      </Fragment>

    );
  }
}

const mapStateToProps = state => ({
  token: state.users.token,
});
const ToExport = withStyles(styles)(Login);
export default connect(mapStateToProps, { performLogin })(ToExport);



