import React, { Component, Fragment } from 'react';
import Lists from './components/Lists';
import Navbar from './components/Navbar';
import NewListForm from './components/NewListForm';
import { LISTS_URL } from './listrUrls';
import { fetchLists } from './actions/listActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
//import './css/bootstrap-material-design.css';

class Main extends Component {

  constructor(props){
    super(props);
    this.onFetchLists = this.onFetchLists.bind(this);
    this.state = { hasLoadedOnce: false }
  }

  onFetchLists(){
    this.props.fetchLists(LISTS_URL, this.props.token);
  }

  componentWillMount(){

  }

  render() {
    var data;
    var sharedData;
    var lists;
    var addButtonStyle = {
      display: 'none'
    };
    //USER LISTS
    if(this.props.token){
      if(this.props.isLoading){
        data = <p>Loading</p>
      }else if(this.props.error){
        data = <p>{this.props.error}</p>
      }
      if(this.state.hasLoadedOnce === false){
        this.props.fetchLists(LISTS_URL, this.props.token);
        this.setState({hasLoadedOnce: true});
      }
      if(this.props.lists){
        data = <Lists data={this.props.lists} key="user"/>
      }
      if(this.props.shared){
        sharedData = <Lists data={this.props.shared} key="shared" listType="shared"/>
      }
    }else{
    }
    const styles = theme => ({
      layout: {
        width: 'auto',
        display: 'block', // Fix IE11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
          width: 400,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
    });

    return (
      <Fragment>
        <CssBaseline />
        <Navbar />
          <Grid item xs={12} style={{padding: 40, marginTop: 10}}>
            <Grid container spacing={8} alignItems="center" justify="center" >
              <Grid item xs={12} style={{marginBottom: 20}}>
                <header className="App-header" style={{display: "inline-flex"}}>
                  <h1 className="App-title">My Pads</h1>
                  <NewListForm />
                  <div style={{ alignSelf: 'center', marginLeft: "10px"}}>
                    <Button onClick={this.onFetchLists}><i className="fa fa-sync-alt"></i></Button>
                  </div>
                </header>
              </Grid>
              <Grid item xs={12} style={{marginBottom: 40, display: "inline"}}>
              {data}
              </Grid>
              <Grid item xs={12}>
                <header className="App-header">
                  <h1 className="App-title">Collab Pads</h1>
                </header>
                <br />
                {sharedData}
              </Grid>
            </Grid>
        </Grid>
      </Fragment>
    );
  }
}

Main.proptypes = {
  fetchLists: PropTypes.func.isRequired,
  lists: PropTypes.array.isRequired,
  shared: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  lists: state.lists.data,
  error: state.lists.error,
  shared: state.lists.sharedData,
  isLoading: state.lists.loading,
});

export default connect(mapStateToProps, { fetchLists })(Main);



