import React, { Component } from 'react';
import "../css/CollabNames.css";
import { performListPost } from '../actions/listActions';
import { LISTS_URL } from '../listrUrls';
import { connect } from 'react-redux';
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Switch from '@material-ui/core/Switch';

import withMobileDialog from '@material-ui/core/withMobileDialog';

class NewListForm extends Component {

  constructor(props){
    super(props);
    this.state = {
      collabs: [],
      listTitle: null,
      open: false,
      currentCollab: "",
      readOnly: false
    }
    this.addItem = this.addItem.bind(this);
    this.listCollabs = this.listCollabs.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.createNewList = this.createNewList.bind(this);
  }

  // LIST COLLABORATORS
  deleteItem(key){
    var filteredItems = this.state.collabs;
    var ind = filteredItems.indexOf(key);
    if(ind > -1){
      filteredItems.splice(ind, 1);
      this.setState({
        collabs:filteredItems
      });
    }
  }
  addItem(e){
    if(this.state.currentCollab !== ""){
      var newCollab = this.state.currentCollab
      };

      this.setState((prevState) => {
        return {
          collabs: prevState.collabs.concat(newCollab)
        };
      });
      this.setState({currentCollab: ""});
      e.preventDefault();
    }
  _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.addItem(e);
    }
  }
  listCollabs(collab){
    return <li onClick={() => this.deleteItem(collab)} >@{collab}</li>
  }
  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  handleClickOpen = (e) => {
    this.setState((prevState) => ({
      open: true,
    }));
  }

  handleClose = (e) => {
    this.setState((prevState) => ({
      open: false,
    }));
  }

  toggleReadOnly = (e) => {
    this.setState({readOnly: !this.state.readOnly});
  }

  //LIST POSTING
  createNewList(e){
    e.preventDefault();
    const listData = {
      "name": this.state.listTitle,
      "static_id": this.makeid(),
      "collabs": this.state.collabs,
      "readOnly": this.state.readOnly
    }
    this.props.performListPost(LISTS_URL, listData, this.props.token);
    this.handleClose();
    this.setState({collabs: [], currentCollab: ""})
  }
  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 6; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }


  render () {
    const { fullScreen } = this.props;
    var collabs = this.state.collabs.map(this.listCollabs);
    var style;
    var inline={
      display: "inline"
    }
    if(this.state.currentCollab !== ''){
      style={
      }
    }else{
      style={
        visibility: "hidden"
      }
    }
    return(
      <div style={{ alignSelf: 'center' }}>
        <Button style={{marginLeft: "10px"}} variant="raised" color="primary" onClick={this.handleClickOpen}><i className="fa fa-plus"></i></Button>
        <Dialog
          fullScreen={fullScreen}
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Create New Pad</DialogTitle>
          <DialogContent>
            <FormControl margin="normal" required fullWidth>
              <InputLabel htmlFor="listrUsername">Name Your Pad</InputLabel>
              <Input id="listTitle" name="text" required autoFocus onChange={(e) => this.handleChange(e)}/>
            </FormControl>
            <FormControl margin="normal" required fullWidth>
              <InputLabel htmlFor="collabs"
              required={false}>Add Some Collabs (by Username)</InputLabel>
              <Input
                name="collabs"
                type="text"
                id="currentCollab"
                autoComplete='off'
                value={this.state.currentCollab}
                onChange={(e) => this.handleChange(e)}
                onKeyPress={this._handleKeyPress}
              />
              <small style={style}>Press 'Enter' after each username to add. You can add multiple collabs</small>
            </FormControl>
            <div class="collabList">
            {collabs}
            </div>
            <p style={inline}>Like Only</p>
            <Switch
              checked={this.state.readOnly}
              onChange={this.toggleReadOnly}
              value="readOnly"
              color="primary"
            />
          <br />
          <small>You can add items too this list, but collabs can only like items.</small>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Cancel
            </Button>
            <Button onClick={this.createNewList} color="primary" variant="raised">
              Create List
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }

}


NewListForm.proptypes = {
  performListPost: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
});

var ToExport = withMobileDialog()(NewListForm);
export default connect(mapStateToProps, { performListPost })(ToExport);



