import React, { Component } from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { performItemPost, deleteItem, fetchItems, likeItem } from '../actions/itemActions';
import { getUserData } from '../actions/userActions';

import { addCollab } from '../actions/listActions';
import { deleteList } from "../actions/listActions";
import { connect } from 'react-redux';
import { ITEMS_URL, USER_URL, LISTS_URL, SHARED_LINK_URL } from '../listrUrls';
import PropTypes from 'prop-types'


import { withStyles } from '@material-ui/core/styles';
import classnames from 'classnames';

import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';

import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';

import FavoriteIcon from '@material-ui/icons/Favorite';
import AddIcon from '@material-ui/icons/Add';
import Icon from '@material-ui/core/Icon';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';

import Chip from '@material-ui/core/Chip';

import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ListSubheader from '@material-ui/core/ListSubheader';
import Grid from '@material-ui/core/Grid';
import withMobileDialog from '@material-ui/core/withMobileDialog';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Button from '@material-ui/core/Button';

import Snackbar from '@material-ui/core/Snackbar';
import SnackbarContent from '@material-ui/core/SnackbarContent';


const styles = theme => ({
  card: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
  },
  actions: {
    display: 'flex',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.danger,
  },
  warningBtn: {
    fontSize: theme.typography.pxToRem(15),
    color: "#ff1744",
  },
});

class ListDetail extends Component {

  constructor(props){
    super(props);
    this.state = {
      removeDialog: false,
      itemName: '',
      itemDescription: '',
      currentCollab: '',
      loadedOnce: false,
      expanded: false,
      detailDialog: false,
      anchorEl: null,
      collabs: [],
      collabsDialog: false,
      username: null,
      url: null,
      snackOpen: false
    }
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.removeList = this.removeList.bind(this);
    this.toggleRemoveList = this.toggleRemoveList.bind(this);
    this.toggleCollabs = this.toggleCollabs.bind(this);
    this.listCollabs = this.listCollabs.bind(this);
    this.deleteCollab = this.deleteCollab.bind(this);
    this.addCollaborators = this.addCollaborators.bind(this);
    this.copyLink = this.copyLink.bind(this);
  }

  //---ITEMS---//
  addItem(e){
    e.preventDefault();
    const itemData = {
      "name": this.state.itemName,
      "static_id": this.makeid(),
    }
    if(this.state.itemDescription){
      itemData.description = this.state.itemDescription;
    }
    this.props.performItemPost(ITEMS_URL, itemData, this.props.data.static_id, this.props.token);
    this.setState({
      itemName: null,
      itemDescription: null,
    });
    document.getElementById('itemName').value = ''
    document.getElementById('itemDescription').value = ''
  }

  deleteItem(e, itemID, listID){
    e.preventDefault();
    this.props.deleteItem(ITEMS_URL, itemID, listID, this.props.token);
  }

  likeItem(e, itemData, listID){
    e.preventDefault();
    fetch(USER_URL, {
      method: 'GET',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Token ' + this.props.token,
      },
    })
    .then(res => res.json())
    .then(data => {
      if(data.username){
        var index = itemData.liked_users.indexOf(data.username);
        if (index > -1) {
          itemData.liked_users = [];
        }else{
          itemData.liked_users.push(data.username);
        }
        this.props.likeItem(ITEMS_URL, itemData, listID, this.props.token);
      }
    });
  }

  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < 8; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  //---LIST---//
  removeList(e, listId){
    if(this.props.token){
      this.props.deleteList(LISTS_URL, listId, this.props.token);
      this.toggleRemoveList();
    }
  }
  listCollabs(collab){
    return <li onClick={() => this.deleteCollab(collab)} >@{collab}</li>
  }
  deleteCollab(key){
    var filteredItems = this.state.collabs;
    var ind = filteredItems.indexOf(key);
    if(ind > -1){
      filteredItems.splice(ind, 1);
      this.setState({
        collabs:filteredItems
      });
    }
  }
  addCollab(e){
    if(this.state.currentCollab !== ""){
      var newCollab = this.state.currentCollab
    };

    this.setState((prevState) => {
      return {
        collabs: prevState.collabs.concat(newCollab)
      };
    });
    this.setState({currentCollab: ''});
    e.preventDefault();
  }
  _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.addCollab(e);
    }
  }

  addCollaborators(e){
    e.preventDefault();
    if(this.state.currentCollab !== null || this.state.currentCollab !== ''){
      addCollab(e);
    }
    const itemData = {
      "collabs": this.state.collabs,
    }
    this.props.addCollab(LISTS_URL, itemData, this.props.data.static_id, this.props.token);
    this.setState({
      currentCollab: '',
      collabs: [],
    });
    this.toggleCollabs();
  }

  copyLink(e, id){
    const el = document.getElementById('url');
    el.value = SHARED_LINK_URL + id;
    el.select();
    this.setState({url: el.value});
    document.execCommand('copy');
    this.handleSnackOpen();
  }

  //---CHANGES IN UI---//

  handleSnackOpen = () => {
    if(this.state.snackOpen !== true){
      this.setState({ snackOpen: true });
    }
  };

  handleSnackClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    if(this.state.snackOpen !== false){
      this.setState({ snackOpen: false });
    }
    this.setState({ url: null });
  };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  toggleRemoveList() {
    this.handleMenuClose();
    this.setState((prevState) => ({
      removeDialog: !prevState.removeDialog,
    }));
  }

  toggleCollabs(){
    if(this.state.collabs.length <= 0){
      this.setState({collabs: this.props.data.collaborators});
    }
    this.setState({collabsDialog: !this.state.collabsDialog})
    this.handleMenuClose();
  }

  handleDetailOpen = (e) => {
    this.props.fetchItems(ITEMS_URL, this.props.data.static_id, this.props.token);
    this.props.getUserData(USER_URL, this.props.token);
    this.setState((prevState) => ({
      detailDialog: true,
    }));
  }

  handleDetailClose = (e) => {
    this.setState((prevState) => ({
      detailDialog: false,
    }));
  }
  handleMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
  };

  expandPanel = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };


  render () {

    const { classes, fullScreen } = this.props;
    var collabs;
    var readOnly;
    var readOnlyDisplay;
    if(this.state.collabs.length > 0){
      collabs = this.state.collabs.map(this.listCollabs);
    }
    var items;
    var hideRemove;
    if(this.props.data.readOnly && this.props.username !== this.props.data.owner){
      readOnly = {
        display: "none",
      };
      readOnlyDisplay = {
        display: "visible",
      };
    }else{
      readOnlyDisplay = {
        display: "none",
      };
    }
    if(this.props.listType !== undefined && this.props.listType === 'shared'){
      hideRemove = {
        display: 'none',
      }
    }
    if(this.props.isOpen && this.props.loading){

      items = (
        <ListItem button>
          <ListItemText primary="This List Has No Items" />
        </ListItem>);
      this.props.fetchItems(ITEMS_URL, this.props.data.static_id, this.props.token);
    }
    if(this.props.items && this.props.items.length > 0){
      items = this.props.items.map(item => {
          var liked = {marginRight: '10px'};
          var color = "default";
          if(item.description !== ""){
            const target = "#"+item.static_id;
            const showNewLine = {
              whiteSpace: "pre-wrap",
              flexGrow: 1
            }
            if(item.liked_users.includes(this.props.username)){
              color = "primary";
            }
            return (
              <ExpansionPanel key={item.static_id} expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography className={classes.heading}>{item.name}</Typography>
                  <Typography className={classes.secondaryHeading}>{item.owner}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                  <Typography style={showNewLine}>
                    {item.description}
                    <hr />
                    <br />
                    <Chip
                      onClick={(e) => this.likeItem(e, item, this.props.data.static_id)}
                      color={color}
                      icon={<FavoriteIcon />}
                      label={item.liked_users.length}
                      style={liked}
                    />
                    <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id, this.props.data.static_id)}>Remove</Button>
                  </Typography>
                </ExpansionPanelDetails>
              </ExpansionPanel>
          );
        }else{
          if(item.liked_users.includes(this.props.username)){
            color = "primary";
          }
          return (
            <ExpansionPanel key={item.static_id} expanded={this.state.expanded === item.static_id} onChange={this.expandPanel(item.static_id)}>
              <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography className={classes.heading}>{item.name}</Typography>
                <Typography className={classes.secondaryHeading}>{item.owner}</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Chip
                  onClick={(e) => this.likeItem(e, item, this.props.data.static_id)}
                  color={color}
                  icon={<FavoriteIcon />}
                  label={item.liked_users.length}
                  style={liked}
                />
                <Button className={classes.warningBtn} onClick={(e) => this.deleteItem(e, item.static_id, this.props.data.static_id)}>Remove</Button>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          );
        }
      });
    }else{
      items = (
        <ListItem button>
          <ListItemText primary="This List Has No Items" />
        </ListItem>);
    }
    const collabDialog = (
      <Dialog
        fullScreen={fullScreen}
        open={this.state.collabsDialog}
        onClose={this.toggleCollabs}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Manage Collabs</DialogTitle>
        <DialogContent>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="collabs"
            required={false}>Add A Collab (by Username)</InputLabel>
            <Input
              name="collabs"
              type="text"
              id="currentCollab"
              autoComplete='off'
              value={this.state.currentCollab}
              onChange={(e) => this.handleChange(e)}
              onKeyPress={this._handleKeyPress}
            />
          <small >Press 'Enter' after each username to add. You can add multiple collabs</small>
          </FormControl>
          <div class="collabList">
          {collabs}
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.toggleCollabs} color="primary">
            Cancel
          </Button>
          <Button onClick={this.addCollaborators} color="primary" variant="raised">
            Update
          </Button>
        </DialogActions>
      </Dialog>);
    const listDialog = (
      <Dialog
        fullScreen={fullScreen}
        open={this.state.detailDialog}
        onClose={this.handleDetailClose}
        aria-labelledby="form-dialog-title"
        >
        <DialogTitle id="form-dialog-title">{this.props.data.name}</DialogTitle>
        <DialogContent>
          <p style={readOnlyDisplay}>You can't add any items to this Pad, but you can view and like items.</p>
          <div style={readOnly}>
            <FormControl margin="normal" required fullWidth>
              <InputLabel htmlFor="itemName">Item Name</InputLabel>
              <Input id="itemName" name="text" required
              value={this.state.itemName} onChange={(e) => this.handleChange(e)}/>
            </FormControl>
            <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="itemDescription"
              required={false}>Item Description (Optional)</InputLabel>
              <Input
                name="itemDescription"
                type="text"
                multiline
                id="itemDescription"
                autoComplete='off'
                value={this.state.itemDescription}
                onChange={(e) => this.handleChange(e)}
              />
            </FormControl>
            <Button
              fullWidth
              variant="raised"
              color="primary"
              onClick={(e) => this.addItem(e)}
              >
              Add Item
            </Button>
          </div>
          <div style={{marginTop: '10px'}}>
            {items}
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleDetailClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>);
    const removeDialog = (
      <Dialog
        fullScreen={fullScreen}
        open={this.state.removeDialog}
        onClose={this.toggleRemoveList}
        aria-labelledby="responsive-dialog-title">
        <DialogTitle id="responsive-dialog-title">{"Are Your Sure You Want to Delete this List?"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            All items will be removed, collaborators won't be able to view it, and you might get sad...
          </DialogContentText>
          <Button onClick={(e) => this.removeList(e, this.props.data.static_id)} autoFocus className={classes.warningBtn}
            fullWidth>
            Yes, Delete This List
          </Button>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.toggleRemoveList} color="">
            Close
          </Button>
        </DialogActions>
      </Dialog>);
    const listOptionsMenu = (
      <Menu
      id="list-options"
      anchorEl={this.state.anchorEl}
      open={Boolean(this.state.anchorEl)}
      onClose={this.handleMenuClose}
      >
        <MenuItem onClick={(e) => this.copyLink(e, this.props.data.static_id)}>Share Link</MenuItem>
        <MenuItem onClick={this.toggleCollabs}>Manage Collabs</MenuItem>
        <MenuItem onClick={this.toggleRemoveList}>Remove List</MenuItem>
        <input style={{position: "absolute", left: "-9999px", top:"-100px"}} type="text" id="url"></input>
      </Menu>);
    return(
      <Grid item key={this.props.data}  xs={12} sm={12} md={4} lg={3}>
        <Card className={classes.card}>
          <CardHeader
          action={
            <IconButton
              aria-owns={this.state.anchorEl ? 'list-optionslist-options' : null}
              aria-haspopup="true"
              onClick={this.handleMenuOpen}
              style={hideRemove}
            >
              <MoreVertIcon />
            </IconButton>
          }
          title={this.props.data.name}
          subheader={this.props.data.owner}
          />
          <CardActions>
            <div>
              <Button onClick={this.handleDetailOpen}>View</Button>
            </div>
          </CardActions>
          {collabDialog}
          {listOptionsMenu}
          {listDialog}
          {removeDialog}
        </Card>
        <div>
          <Snackbar
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
            open={this.state.snackOpen}
            autoHideDuration={3000}
            onClose={this.handleSnackClose}
            ContentProps={{
              'aria-describedby': 'message-id',
            }}
            message={<span id="message-id">Copied Pad to Clipboard: {this.state.url}</span>}
            action={[
              <IconButton
                key="close"
                aria-label="Close"
                color="inherit"
                className={classes.close}
                onClick={this.handleSnackClose}
              >
                <CloseIcon />
              </IconButton>,
            ]}
          /></div>
      </Grid>
    );
  }
}

ListDetail.proptypes = {
  performItemPost: PropTypes.func.isRequired,
  addCollab: PropTypes.func.isRequired,
  deleteItem: PropTypes.func.isRequired,
  getUserData: PropTypes.func.isRequired,
  deleteList: PropTypes.func.isRequired,
  likeItem: PropTypes.func.isRequired,
  fetchItems: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  fullScreen: PropTypes.bool.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
  loading: state.items.loading,
  items: state.items.items,
  username: state.users.username
});

var ToExport = withMobileDialog()(ListDetail);
ToExport = withStyles(styles)(ToExport);
export default connect(mapStateToProps, { performItemPost, deleteItem, fetchItems, likeItem, addCollab, deleteList, getUserData })(ToExport);



